import bus from "../bus"
export default {
    name:"login",
    data:function(){
        return {
            phone:0,
            yzm:0,
            send:false,
        }
    },
    methods:{
       change_border(e){
           $(".input").removeClass("input_active input_error")
        //    console.log(e.target)
           $(e.target.parentElement).addClass("input_active")
       },
        // 输入手机号
        write_phone(){
            var phone= $("input").eq(0).val()
            this.phone=phone
            var pattern = /^[0-9]{11}$/;
            var result=pattern.test(phone)
            if(result){
                $("#get_yzm").css({"color":"#2395ff"}).prop("disabled","")
                this.send=true
            }else{
                $("#get_yzm").css({"color":"#ccc"}).prop("disabled","disabled")
            }
        },
       get(){
           if(this.send){
                var code = (Math.random() * 1000000).toFixed(0)
                console.log(code)
                this.yzm=code
                var daojishi=10
                // 发送请求
                // this.$http.get("http://sms.tehir.cn/code/sms/api/v1/send?srcSeqId=123&account="+this.phone
                //                 +"&password=" + "111111"
                //                 +"&mobile=" + this.phone
                //                 + "&code=" + this.yzm
                //                 + "&time=15").then(function(resdata){
                //                     console.log(resdata)
                //                 })

                var timer= setInterval(function(){
                    daojishi--
                    // console.log(daojishi)
                    $("#get_yzm").css({"cursor":"not-allowed"}).text("已发送(" + daojishi + "s)")
                        if (daojishi == 0) {
                            clearInterval(timer)
                            $("#get_yzm").text("重新获取验证码").css({"cursor":"pointer","color":"#2395ff","fontSize":".75rem"}).prop("disabled", "")
                        }
                },1000)
            }else{
                
                this.$vux.toast.show({
                    text: '请输入正确手机号'
                })
            } 
        },
        log(){
            var input_code=$("input").eq(1).val()
            if (input_code==this.yzm) {
                // console.log("验证成功!")
                this.$vux.toast.show({
                    text: '验证成功'
                })
                this.$router.push({path:"/my",query:{phone:this.phone}})
                // console.log(this.$router)
            }else{
                this.$vux.toast.show({
                    type:"cancel",
                    text: '登录失败'
                })
                $(".input").eq(1).removeClass("input_active").addClass("input_error")
            }
        }
    },
    beforeMount() {
        bus.$emit("showBar",false)
    },
 
}



