import backHeader from "./back_header/header.vue";
import bus from "../bus"
export default {
  components: {
    backHeader
  },
  data: function() {
    return {
      tit: "发现",
      gift_list:null
    };
  },
  beforeMount() {
        bus.$emit("showBar",true) 
        this.$http.get("https://mainsite-restapi.ele.me/member/gifts/suggest",{
            parmas:{
                limit:3
            }
        }).then(function(resdata){
          console.log(resdata.body)
          this.gift_list=resdata.body
      })
  },
};



