export default {
    name:"storecard",
    props:["foodlist","price"],
    data:function(){
        return{
            count:0
        }
    },
    methods:{
        jian(){
            if (this.count > 0) {
                this.count--
                // 乘随机数是为了让watch每次都能监听到值的变化
                this.$emit("input", (-1) * this.price + Math.random()*0.009)
            }else{
                this.count=0
            }
           
        },
        jia(){
            this.count++;
            this.$emit("input", 1 * this.price + Math.random()*0.009)
        }
    },
    beforeMount() {
        // console.log(this.foodlist)
    },
}



