export default {
    name:"pingjia",
    data:function(){
        return{
            id:0,
            score_list:null,
            rate_list:null
        }
    },
    beforeMount() {
        this.id=this.$route.params.id
        // https://mainsite-restapi.ele.me/ugc/v2/restaurants/E11527992432886207451/ratings/scores
        this.$http.get("https://mainsite-restapi.ele.me/ugc/v2/restaurants/"+this.id +"/ratings/scores").then(function(resdata){
            // console.log(resdata.body)
            this.score_list=resdata.body
        })
        // https://mainsite-restapi.ele.me/ugc/v2/restaurants/E11527992432886207451/ratings/tags
        this.$http.get("https://mainsite-restapi.ele.me/ugc/v2/restaurants/"+this.id+"/ratings/tags").then(function(rate_data){
            // console.log(rate_data.body)
            this.rate_list=rate_data.body
        })
    },
    mounted() {

    },
}



