

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import Home from './components/HelloFromVux'
import First from './components/first'
import Find from './components/find'
import Dingdan from './components/dingdan'
import My from './components/my'
import Store from './components/store'
import Login from './components/login'

import VueResource from 'vue-resource'
Vue.use(VueResource)

import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

Vue.component('tab', Tab)
Vue.component('tab-item', TabItem)

import Card from 'vux/src/components/card/index.vue'

Vue.component('card', Card)


import Loading from 'vux/src/components/loading/index.vue'

Vue.component('loading', Loading)
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use(LoadingPlugin)


import Badge from 'vux/src/components/badge/index.vue'

Vue.component('badge', Badge)


import Toast from 'vux/src/components/toast/index.vue'

Vue.component('toast', Toast)
import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin)


import $ from "jquery"

Vue.use(VueRouter)


const routes = [{
path: '/',
component: Home
},
{
path: '/first',
component: First,
name:"first"
},
{
path: '/find',
component: Find,
name:"find"
},
{
path: '/dingdan',
component: Dingdan,
name:"dingdan"
},
{
path: '/my',
component: My,
name:"my"
},
{
path: '/first/store/:id',
component: Store,
name:"store"
},
{
path: '/login',
component: Login,
name:"login"
},
]

const router = new VueRouter({
routes
})

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')



function calcSize(){
var width=document.documentElement.clientWidth
var height=document.documentElement.clientHeight


var html=document.querySelector("html")
html.style.fontSize=width / 360  * 16 +"px"
}

calcSize()


window.onresize=function(){
calcSize()
}

router.push("/first")






