import bus from "./bus"
export default {
  name: "app",
  data:function(){
    return{
      Barshow:true
    }
  },
  beforeMount() {
    bus.$on("showBar",function(val){
      this.Barshow=val
      // 注意这里的this指向问题啊!!
    }.bind(this))


    var Days = 30; 
    var exp = new Date(); 
    exp.setTime(exp.getTime() + Days*24*60*60*1000); 
    document.cookie = 'SID' + "="+ escape ('bTxBAawXOKYEc76PrRVgffDbnePVdUJQZeag') + ";expires=" + exp.toGMTString();
  },
};





